import React, { useState, useEffect } from 'react';
// eslint-disable-next-line
import { VKApi, ConsoleLogger } from 'node-vk-sdk';
import ConfigProvider from '@vkontakte/vkui/dist/components/ConfigProvider/ConfigProvider';
import connect from '@vkontakte/vk-connect';
import Root from '@vkontakte/vkui/dist/components/Root/Root';
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
// eslint-disable-next-line
import Persik from './panels/Persik';
import Official from './panels/Official';
import InfoWM from './panels/InfoWM';

import Phederal from './panels/persons/Phederal';
import GJchester from './panels/persons/GJchester';
import Blindkid from './panels/persons/Blindkid';
import Filliner from './panels/persons/Filliner';
import Nvn from './panels/persons/NVN';
import Savnko from './panels/persons/Savnko';



const App = () => {
	const [activePanel, setActivePanel, history] = useState('home');
	const [fetchedUser, setUser] = useState(null);
	const [popout, setPopout] = useState(<ScreenSpinner size='large' />);
	const [activeView, setActiveView] = useState('main');

	useEffect(() => {
		connect.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'alternate';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
		async function fetchData() {
			const user = await connect.sendPromise('VKWebAppGetUserInfo');
			setUser(user);
			setPopout(null);
		}
		fetchData();
	}, []);

	const go = e => {
		setActivePanel(e.currentTarget.dataset.to);
	};

	const gov = v => {
		setActiveView(v.currentTarget.dataset.to);
		setActivePanel(v.currentTarget.dataset.tp);
	};

	const goBack = () => {
		const history = [...this.state.history];
	    history.pop();
	    const activePanel = history[history.length - 1];
	    if (activePanel === 'home') {
	      connect.send('VKWebAppDisableSwipeBack');
	    }
	    this.setState({ history, activePanel });
	};

	const goForward = (activePanel) => {
		const history = [...this.state.history];
	    history.push(activePanel);
	    if (this.state.activePanel === 'home') {
	      connect.send('VKWebAppEnableSwipeBack');
	    }
	    this.setState({ history, activePanel });
	};


	return (
	<ConfigProvider isWebView={true}>
	<Root activeView={activeView}>

		<View activePanel={activePanel} id='main' modal={(null)} onSwipeBack={goBack} onSwipeBackStart={goForward} history={history}>
			<Home id='home' fetchedUser={fetchedUser} go={go} gov={gov} />
			<Official id='official' go={go} gov={gov} />
		</View>

		<View activePanel='infowm' id='info' modal={(null)} onSwipeBack={goBack} onSwipeBackStart={goForward} history={history} popout={popout}>
			<InfoWM id='infowm' go={go} gov={gov} />
		</View>

		<View activePanel={activePanel} id='persons' modal={(null)} onSwipeBack={goBack} onSwipeBackStart={goForward} history={history}>
			<Phederal id='phederal' go={go} gov={gov} />
			<GJchester id='gj' go={go} gov={gov} />
			<Blindkid id='blindkid' go={go} gov={gov} />
			<Filliner id='filliner' go={go} gov={gov} />
			<Nvn id='nvn' go={go} gov={gov} />
			<Savnko id='savnko' go={go} gov={gov} />
		</View>

	</Root>
	</ConfigProvider>
	);
}

export default App;

